import './Comm.css'
import React, { Component } from 'react'
import {NavLink} from 'react-router-dom'
export default class Home extends Component {
    constructor(){
        super();
        this.state={
          shares:[],
        }
    }
    componentDidMount(){
        fetch('/share',{
            method:'get',
            mode:'cors',
            headers:{'Content-Type':'application/json'},
        }).then(res=>res.json())
        .then(res=>{
            this.setState({shares:res})
        })
    }
    reload(){
        this.props.history.go(0);
    }
    
    render() {
        return(<div>
        <div>
            <ol className='t1'>
                <li>头像</li>
                <li>朋友圈id</li>
                <li style={{width:80}}>昵称</li>
                <li style={{width:550}}>朋友圈内容</li>
                <li style={{width:160}}>发帖时间</li>
                <li style={{width:70}}>查看评论</li>
                <li style={{width:80}}>删除朋友圈</li>
            </ol>
        </div>
        <div>
            {
                (this.state.shares==0)?null:
                this.state.shares.map((share)=>{
                    let id=share.shid;
                    return(
                        <ol className='c1'>
                            <li><img src={share.shimg} className='tu'/></li>
                            <li>{share.shid}</li>
                            <li style={{width:80}}>{share.shname}</li>
                            <li style={{width:550}}>{share.shtext}</li>
                            <li style={{width:160}}>{share.stime}</li>
                            <li style={{width:70}}><NavLink style={{color:'black'}} to={{pathname:'/comment',state:{id}}}>查看评论</NavLink></li>
                            <li style={{width:80}}><button onClick={()=>{
                                fetch('/delshare',{
                                    method:'post',
                                    mode:'cors',
                                    headers:{'Content-Type':'application/json'},
                                    body:JSON.stringify({"shid":share.shid}),
                                }).then(setTimeout(this.reload.bind(this),200))
                            }
                            }>删除</button></li>
                        </ol>
                    )
                })
            }
        </div>
    </div>)
    }
}