import React, { Component } from 'react'
import './Guide.css'
export default class Guide extends Component {
    constructor(){
        super();
        this.state={
          goods:[],
        }
    }
    componentDidMount(){
        if(!this.props.location.state){
            fetch('/good',{
                method:'get',
                mode:'cors',
                headers:{'Content-Type':'application/json'},
            }).then(res=>res.json())
            .then(res=>{
                this.setState({goods:res})
            })
        }
        else{
            let id=this.props.location.state.id        
            fetch('/searchstore/'+encodeURI(id),{
                method:'post',
                mode:'cors',
                headers:{'Content-Type':'application/json'},
            }).then(res=>res.json())
            .then(res=>{
                this.setState({goods:res})
            })
        }
    }
    reload(){
        this.props.history.go(0);
    }
    render() {
        return(<div>
        <div>
            <ol className='gt'>
                <li>商品照片</li>
                <li>商品id</li>
                <li>商品名称</li>
                <li>商品库存</li>
                <li>商品单位</li>
                <li>商品价格</li>
                <li style={{width:80}}>删除商品</li>
            </ol>
        </div>
        <div>
            {
                (this.state.goods==0)?null:
                this.state.goods.map((good)=>{
                    return(
                        <ol className='gc'>
                            <li><img src={good.gimgsrc} className='tu'/></li>
                            <li>{good.gid}</li>
                            <li>{good.gtitle}</li>
                            <li>{good.gresidue}</li>
                            <li>{good.gunit}</li>
                            <li>{good.gprice}</li>
                            <li style={{width:80}}><button onClick={()=>{
                                fetch('/gdel',{
                                    method:'post',
                                    mode:'cors',
                                    headers:{'Content-Type':'application/json'},
                                    body:JSON.stringify({"gid":good.gid}),
                                }).then(setTimeout(this.reload.bind(this),200))
                            }
                            }>删除</button></li>
                        </ol>
                    )
                })
            }
        </div>
    </div>)
    }
}