import './Tuto.css'
import React, { Component } from 'react'

export default class Tuto extends Component {
    constructor(){
        super();
        this.state={
          users:[],
        }
    }
    componentDidMount(){
        fetch('/user',{
            method:'get',
            mode:'cors',
            headers:{'Content-Type':'application/json'},
        }).then(res=>res.json())
        .then(res=>{
            this.setState({users:res})
        })
    }
    render() {
        return(<div>
        <div>
            <ol className='title'>
                <li>用户id</li>
                <li>用户姓名</li>
                <li>用户电话</li>
                <li style={{width:400}}>用户地址</li>
                <li>地址详情</li>
            </ol>
        </div>
        <div>
            {
                (this.state.users==0)?null:
                this.state.users.map((user)=>{
                    return(
                        <ol className='con'>
                            <li>{user.uid}</li>
                            <li>{user.uname}</li>
                            <li>{user.utel}</li>
                            <li style={{width:400}}>{user.uadd}</li>
                            <li>{user.uaddde}</li>
                        </ol>
                    )
                })
            }
        </div>
    </div>)
    }
}