import './Blog.css'
import React, { Component } from 'react'

export default class Blog extends Component {
    constructor(){
        super();
        this.state={
          texts:[],
        }
    }
    componentDidMount(){
        fetch('/text',{
            method:'get',
            mode:'cors',
            headers:{'Content-Type':'application/json'},
        }).then(res=>res.json())
        .then(res=>{
            this.setState({texts:res})
        })
    }
    reload(){
        this.props.history.go(0);
    }
    render() {
        return(<div>
            <div>
                <ol className='ti'>
                    <li>留言id</li>
                    <li>所留言商店id</li>
                    <li>留言时间</li>
                    <li style={{width:600}}>留言内容</li>
                    <li style={{width:80}}>删除留言</li>
                </ol>
            </div>
        <div>
            {
                (this.state.texts==0)?null:
                this.state.texts.map((text)=>{
                    return(
                        <ol className='co'>
                            <li>{text.tid}</li>
                            <li>{text.sid}</li>
                            <li>{text.ttime}</li>
                            <li style={{width:600}}>{text.text}</li>
                            <li style={{width:80}}><button onClick={()=>{
                                fetch('/deltext',{
                                    method:'post',
                                    mode:'cors',
                                    headers:{'Content-Type':'application/json'},
                                    body:JSON.stringify({"tid":text.tid}),
                                }).then(setTimeout(this.reload.bind(this),200))
                            }
                            }>删除</button></li>
                        </ol>
                    )
                })
            }
        </div>
    </div>)
    }
}