import './Core.css'
import React, { Component } from 'react'
import {NavLink} from 'react-router-dom'
export default class Core extends Component {
    constructor(){
        super();
        this.state={
          stores:[],
        }
    }
    componentDidMount(){
        fetch('/store',{
            method:'get',
            mode:'cors',
            headers:{'Content-Type':'application/json'},
        }).then(res=>res.json())
        .then(res=>{
            this.setState({stores:res})
        })
    }
    reload(){
        this.props.history.go(0);
    }
    render() {
        return(<div>
        <div>
            <ol className='t'>
                <li>超市照片</li>
                <li>商店id</li>
                <li>联系人姓名</li>
                <li>商店名称</li>
                <li style={{width:100}}>联系电话</li>
                <li style={{width:350}}>超市地址</li>
                <li style={{width:100}}>查看商品</li>
                <li style={{width:100}}>查看订单</li>
                <li style={{width:100}}>查看留言</li>
                <li style={{width:80}}>删除商店</li>
            </ol>
        </div>
        <div>
            {
                (this.state.stores==0)?null:
                this.state.stores.map((store)=>{
                    let id = store.sid;
                    return(
                        <ol className='c'>
                            <li><img src={store.spic} className='tu'/></li>
                            <li>{store.sid}</li>
                            <li>{store.sname}</li>
                            <li>{store.sshopname}</li>
                            <li style={{width:100}}>{store.stel}</li>
                            <li style={{width:350}}>{store.sadd}</li>
                            <li style={{width:100}}><NavLink  to={{pathname:'/doc/guide',state:{id}}}>查看商品</NavLink></li>
                            <li style={{width:100}}><NavLink  to={{pathname:'/doc/api',state:{id}}}>查看订单</NavLink></li>
                            <li style={{width:100}}><NavLink  to={{pathname:'/doc/hooks',state:{id}}}>查看留言</NavLink></li>
                            <li style={{width:80}}><button onClick={()=>{
                                fetch('/sdel',{
                                    method:'post',
                                    mode:'cors',
                                    headers:{'Content-Type':'application/json'},
                                    body:JSON.stringify({"sid":store.sid}),
                                }).then(setTimeout(this.reload.bind(this),200))
                            }
                            }>删除</button></li>
                        </ol>
                    )
                })
            }
        </div>
    </div>)
    }
}