import React from 'react'
import { NavLink } from 'react-router-dom'
import './Header.css'
const Header = () => {
    return (
        <header>
            <nav>
                <NavLink activeClassName='active' exact to='/'>Home</NavLink>
                <NavLink activeClassName='active' to='/doc/core'>商家</NavLink>
                <NavLink activeClassName='active' to='/tuto'>收货地址</NavLink>
                <NavLink activeClassName='active' to='/blog'>留言</NavLink>
                <NavLink activeClassName='active' to='/comm'>水果圈</NavLink>
                <NavLink activeClassName='active' to='/list'>订单</NavLink>
            </nav>
        </header>
    ) 
}
export default Header