import './Blog.css'
import React, { Component } from 'react'

export default class Hook extends Component {
    constructor(){
        super();
        this.state={
          texts:[],
        }
    }
    componentDidMount(){
        if(!this.props.location.state){
            fetch('/text',{
                method:'get',
                mode:'cors',
                headers:{'Content-Type':'application/json'},
            }).then(res=>res.json())
            .then(res=>{
                this.setState({texts:res})
            })
        }
        else{
            let id=this.props.location.state.id
            console.log(id)        
            fetch('/searcht/'+encodeURI(id),{
                method:'post',
                mode:'cors',
                headers:{'Content-Type':'application/json'},
            }).then(res=>res.json())
            .then(res=>{
                console.log(res)
                this.setState({texts:res})
            })
        }
    }
    reload(){
        this.props.history.go(0);
    }
    render() {
        return(<div>
            <div>
                <ol className='ti'>
                    <li>留言id</li>
                    <li>留言时间</li>
                    <li style={{width:600}}>留言内容</li>
                    <li style={{width:80}}>删除留言</li>
                </ol>
            </div>
        <div>
            {
                (this.state.texts==0)?null:
                this.state.texts.map((text)=>{
                    return(
                        <ol className='co'>
                            <li>{text.tid}</li>
                            <li>{text.ttime}</li>
                            <li style={{width:600}}>{text.text}</li>
                            <li style={{width:80}}><button onClick={()=>{
                                fetch('/deltext',{
                                    method:'post',
                                    mode:'cors',
                                    headers:{'Content-Type':'application/json'},
                                    body:JSON.stringify({"tid":text.tid}),
                                }).then(setTimeout(this.reload.bind(this),200))
                            }
                            }>删除</button></li>
                        </ol>
                    )
                })
            }
        </div>
    </div>)
    }
}